.cart {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    gap: 25px;

    margin-top: 50px;
    height: 100%;
    width: 100%;
    max-width: 650px;
}

.cart h1 {
    font-size: 1.7rem;
}

.cart_items {
    border-spacing: 0;
    padding: 10px 20px;
    text-align: center;
    width: 100%;
}

.cart_items thead {
    font-size: 0.85rem;
}

.cart_items thead tr th {
  border-bottom: 2px solid black;
}

.th_product {
    text-align: start;
}

@media only screen and (min-width: 500px) {
    .cart_items thead {
        font-size: 1rem;
    }
}